import { create } from "zustand";

const initialUser = {
  id: "",
  name: "",
  studentId: "",
  department: "",
  avatar: "",
  language: "en",
  isAdmin: false,
};

const useGameStore = create((set, get) => ({
  // ============================================================
  // USER
  // ============================================================
  user: initialUser,

  setUser: (partial) =>
    set((state) => ({
      user: { ...state.user, ...partial },
    })),

  setLanguage: (language) =>
    set((state) => ({
      user: { ...state.user, language },
    })),

  // ============================================================
  // GAME STATS (  )
  // ============================================================
  gameStats: {
    level: 1,
    experience: 0,        //  GameHeader 
    coins: 0,             //  GameHeader 
    streak: 0,
    questionsAnswered: 0,

    //      survey fetch  update 
    totalQuestions: 168,  //  GameHeader 

    currentChapter: 1,    //  GameHeader 
  },

  //  /  /   
 //  /  /   
  answerQuestion: (timeSpent, quality) => {
    const stats = get().gameStats;
    let exp = 10;
    let coins = 1;

    if (quality === "perfect") {
      exp = 20;
      coins = 3;
    } else if (quality === "good") {
      exp = 15;
      coins = 2;
    }

    if (timeSpent < 3) {
      exp += 5; // 
      coins += 1;
    }

    let newExp = stats.experience + exp;
    const newCoins = stats.coins + coins;
    let newLevel = stats.level;

    //   : 100    
    while (newExp >= 100) {
      newExp -= 100; //  100 
      newLevel += 1; // 
    }

    set({
      gameStats: {
        ...stats,
        experience: newExp,
        coins: newCoins,
        questionsAnswered: stats.questionsAnswered + 1,
        level: newLevel,
      },
    });

    const levelUp = newLevel > stats.level;

    return { exp, coins, levelUp };
  },

  updateStreak: (correct) =>
    set((state) => ({
      gameStats: {
        ...state.gameStats,
        streak: correct ? state.gameStats.streak + 1 : 0,
      },
    })),

  setTotalQuestions: (count) =>
    set((state) => ({
      gameStats: {
        ...state.gameStats,
        totalQuestions: count,
      },
    })),

  completeChapter: (chapter) => {
    console.log(" :", chapter);
  },

  resetGame: () =>
    set({
      gameStats: {
        level: 1,
        experience: 0,
        coins: 0,
        streak: 0,
        questionsAnswered: 0,
        totalQuestions: 168,
        currentChapter: 1,
      },
    }),
}));

export default useGameStore;
